import { ReactiveFormsModule } from '@angular/forms';

@NgModule({
  imports: [
    CommonModule,
    ManageSubCatRoutingModule,
    ReactiveFormsModule,
    CoreModule
  ],
  declarations: [ManageSubCatComponent],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]

})
export class ManageSubCatModule { }



