import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Modal } from 'angular2-modal';
import { CompleterData, CompleterService } from 'core/autocomplete';
import { Constants } from 'core/constants';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { LocalStorageService } from 'core/service/local-storage.service';

@Component({
  selector: 'app-manage-sub-cat',
  templateUrl: './manage-sub-cat.component.html',
  styleUrls: ['./manage-sub-cat.component.css']
})
export class ManageSubCatComponent implements OnInit {
  subCatId:any;
  selectedSubCat:any;
  public editingSubCategory:boolean;
  subCategoryForm:FormGroup;
  public isDropinSelected:boolean;
  pacakgeTypeListWithoutDROPIN=["GENERAL"];
  pacakgeTypeListWithDROPIN=["GENERAL","DROPIN","GYMSPECIFIC"];

  pacakgeTypeListGymSpecific=["GYMSPECIFIC"];
  pacakgeClassType=["LIMITED","UNLIMITED"];


  pacakgeTypeList=["GENERAL","DROPIN","GYMSPECIFIC"];
  validityPeriodListUnlimited=["UNLIMITED"];
  validityPeriodListNormal=["DAYS","WEEKS","MONTHS"];
  validityPeriodList=["DAYS","WEEKS","MONTHS"];
  currentPackage
  alradyHaveDropin:boolean;
  activityList:any[]=[ ];
  dataService: CompleterData;
  public isFieldValid
  public activitiesList=[];
  public deletedActivitiesList=[];
  public deletedActivitiesList1=[];
  public isMasterGym;
  public isCreditPackage=['YES','NO']


  constructor(private completerService: CompleterService,private http_common: HttpCommonUtilsService,private _userAuthHttpService: HttpUserAuthUtilsService,private route: ActivatedRoute,private router: Router,private http_userauth: HttpUserAuthUtilsService,private modal: Modal,public formBuilder: FormBuilder) { }

  ngOnInit() {
    this.isMasterGym = LocalStorageService.isMasterGym();
      this.subCategoryForm = this.formBuilder.group({
        name: ['', [Validators.required]],
        catId: ['',Validators.required],
        packageType: ['GENERAL', [Validators.required]],
        errorMessage:[''],
        gymId:[LocalStorageService.getUserId(),Validators.required]
  
      });
     this.route.params.subscribe(params => {
      
      if(params['id']){
        ""
        this.subCatId = params['id'] || null;
        this.editingSubCategory=true;
        this.getCategoryList();

      }else{
        this.getCategoryList();

      }
    });


  //  this.http_userauth.getAllActivity(this,true)

  }
  getPackageList() {
        this._userAuthHttpService.getAllMembershipPackages(this,true,-1);


}

getSubCategoryData(){
this.http_userauth.getAllSubCategory(this,true);
}

   getCategoryList()
   {
     this.http_common.getAllActivity(this,true,1)

   }

   addSubActivity(){
     if(this.subCategoryForm.valid){
      let request={"name":this.subCategoryForm.get("name").value,
      "classCategoryId":this.subCategoryForm.get("catId").value,
      "packageType":this.subCategoryForm.get("packageType").value,
      "userId":this.subCategoryForm.get("gymId").value
      
      }
this._userAuthHttpService.addSubCategory(request,this,true)
     }else{
       this.markFormGroupTouched(this.subCategoryForm)
     }
   }
   editSubActivity(){
     ""
    if(this.subCategoryForm.valid){
    let request={"name":this.subCategoryForm.get("name").value,
"classCategoryId":this.subCategoryForm.get("catId").value,
"packageType":this.subCategoryForm.get("packageType").value,
"userId":this.subCategoryForm.get("gymId").value

}
this._userAuthHttpService.editSubCategory(this.subCatId,request,this,true)
    }else{
      this.markFormGroupTouched(this.subCategoryForm)
    }
  }
onSuccess(type: any, responsedata: any) {
 if(type==Constants.AllActivityListApi){
  this.activitiesList = responsedata.data;
  if(this.editingSubCategory){
    this.getSubCategoryData();
  }
 }
 if(type==Constants.EditSubCategoryApi){
  this.router.navigate(['listSubCategory'])

}
  if(type==Constants.AddSubCategoryApi){
    this.router.navigate(['listSubCategory'])

 }
 if(type==Constants.getSubCategoryApi){
  responsedata.data.forEach(element => {
     if(this.subCatId==element.id){
       this.selectedSubCat = element;
       
       console.log("Found subcat");
       this.setupdataForEdit();
     }
   });
 }
 
}
onFailure(type: any, response: any) {
  if(type==Constants.AllActivityListApi){
   
  }
  if(type==Constants.getSubCategoryApi){
   
  }
  if(type==Constants.AddSubCategoryApi){
    ""
    this.subCategoryForm.patchValue({errorMessage:response})
    this.subCategoryForm.updateValueAndValidity();

    setTimeout(() => {
      this.subCategoryForm.patchValue({errorMessage:""})
      this.subCategoryForm.updateValueAndValidity();

  
    }, 2000);
  }
  if(type==Constants.EditSubCategoryApi){
    ""

    this.subCategoryForm.patchValue({errorMessage:response})
    this.subCategoryForm.updateValueAndValidity();

    setTimeout(() => {
      this.subCategoryForm.patchValue({errorMessage:""})
      this.subCategoryForm.updateValueAndValidity();

  
    }, 2000);
  }
}


  private setupdataForEdit(){
    try{
      ""
      this.subCategoryForm.setValue({
        name: this.selectedSubCat.name,
        catId: this.selectedSubCat.classCategoryResponseDto.id,
        packageType: 'GENERAL',
        errorMessage:'',
        gymId:LocalStorageService.getUserId()
     });
    
    }catch(e){

    }
 
    
    this.subCategoryForm.updateValueAndValidity();
    
  }


  private markFormGroupTouched(formGroup: FormGroup) {
    try {
      (<any>Object).values(formGroup.controls).forEach(control => {
        control.markAsTouched();
        if (control.controls) {
          control.controls.forEach(c => this.markFormGroupTouched(c));
        }
      });
    } catch (ex) {
      // Code to handle exception
    }


}
}





